.Score {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  text-align: center;
  height: 100%;
  width: 100%;
  -webkit-animation: slide-in-right 0.5s cubic-bezier(0.230, 1.000, 0.320, 1.000) both;
          animation: slide-in-right 0.5s cubic-bezier(0.230, 1.000, 0.320, 1.000) both;
}

.Score-exiting {
  -webkit-animation: slide-out-left 0.5s cubic-bezier(0.230, 1.000, 0.320, 1.000) both;
          animation: slide-out-left 0.5s cubic-bezier(0.230, 1.000, 0.320, 1.000) both;
}

.Score-label {
  color: white;
  font-size: 30px;
  margin-right: 20px;
  font-weight: bold;
}

.Score-points {
  background: white;
  padding: 2px 20px;
  font-size: 30px;
  font-weight: bold;
  border-radius: 36px;
}

.Score-retry {
  position: absolute;
  bottom: 100px;
  -webkit-animation: scale-in-center 0.2s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
          animation: scale-in-center 0.2s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
  -webkit-transform: scale(0);
      -ms-transform: scale(0);
          transform: scale(0);
  background: white;
  border-radius: 36px;
  padding: 2px 20px;
  font-weight: bold;
  font-size: 22px;
  border: 3px solid;
  cursor: pointer;
  -webkit-box-shadow: 0 0 0 3px white, 0 6px 20px 0 rgba(0, 0, 0, 0.19);
          box-shadow: 0 0 0 3px white, 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  -webkit-transition: all .2s ease-in-out;
  -o-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
}

.Score-retry:hover {
  font-size: 26px;
  border: 4px solid;
  padding: 3px 22px;
  -webkit-transform: scale(2);
      -ms-transform: scale(2);
          transform: scale(2);
}

.Score-menu {
  position: absolute;
  font-size: 22px;
  bottom: 40px;
  color: white;
  -webkit-animation: scale-in-center 0.2s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
          animation: scale-in-center 0.2s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
  -webkit-animation-delay: 1.4s;
          animation-delay: 1.4s;
}


